@import "utils";

.nav {
  &__toggler {
    display: none;

    &:not(:checked) ~ .nav__list {
      display: none;
    }

    &:not(:checked) ~ * .bg-div:nth-child(1) {
      left: 25px;
    }

    &:not(:checked) ~ * .bg-div:nth-child(2) {
      top: 8px;
      left: 25px;
    }

    &:not(:checked) ~ * .bg-div:nth-child(3) {
      top: 16px;
      left: 25px;
    }

    &:checked ~ * .bg-div:first-child {
      display: none;
    }

    &:checked ~ * .bg-div:nth-child(2) {
      transform: rotate(45deg);
    }

    &:checked ~ * .bg-div:nth-child(3) {
      transform: rotate(-45deg);
    }
  }

  &__toggle-label {
    display: block;
    width: 50px;
    height: 50px;
    position: relative;
    transition: all 0.4s;
    margin: 0 auto;
/*stylelint-disable*/
    & .bg-div {
      background-color: #fff;
      width: 40px;
      margin-top: 20px;
      position: absolute;
      height: 5px;
      transition: all 0.4s;
      left: 5px;
      transform: translate(-50%,0);
    }

    @media (min-width: 768px) {
      display: none;
    }
  }

  &__list {
    display: flex;
    justify-content: center;
    width: 100%;
    align-items: flex-start;
  }
/*stylelint-enable*/
  &__item {
    margin-right: 63px;
    margin-top: 3px;
  }

  &__item:first-child {
    margin-right: 143px;
    margin-top: 0;

    & .link {
      font-size: 32px;
      line-height: 100%;
    }
  }

  &__item:last-child {
    margin-right: 0;
    margin-left: 62px;
  }

  &__link {
    text-decoration: none;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 150%;
    color: #fff;
    white-space: nowrap;
    position: relative;
    &:before {
      position: absolute;
      content: "";
      display: none;
      width: calc(100% + 10px);
      height: 4px;
      /*stylelint-disable*/
      background: linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%, rgba(255, 255, 255, 0) 100%), #fcc304;
      /*stylelint-enable*/
      border-radius: 32px;
      bottom: -5px;
      transform: translate(-5px, 0);
    }

    &:hover {
      &:before {
        display: block;
      }
    }

    &-hoover {
      &:before {
        display: block;
      }
    }
  }

}
